@media (min-width: $desktop-w) {
  .record {
    display: none;
    background-image: url("../img/back_second.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .record__wrap {
    padding-top: 30px;
    padding-left: 100px;
    width: 100%;
  }//
  .record__logo {
    width: 15%;
    height: 15%;
  }
  .record__title {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 48px;
    font-weight: 700;
    text-align: center;
    color: $text-btn;
    margin-bottom: 100px;
  }
  .record__calendar--wrap {
    display: flex;
    flex-wrap: nowrap;
  }

  .record__list {
  }

  .record__elem {
  }
  .record__calendar {

  }
}


.calendar {
  width: 100%;
  border: 2px dashed #c0c0c0;
}

.calendar__body {
  width: 100%;
  line-height: 20px;
  font-size: 14px;
  text-align: center;
}

.calendar__arrow {
  font-size: 30px;
  color: #0e708d;
  cursor: pointer;
  padding-bottom: 15px;
}

.calendar__month {
  font-size: 20px;
  font-weight: 700;
  color: #0e708d;
}

.calendar__day {
  font-size: 20px;
  font-weight: 700;
}

#calendar tbody td {
  color: rgb(44, 86, 122);
  font-weight: 700;
}

#calendar tbody td:nth-child(n+6), #calendar .holiday {
  color: rgb(231, 140, 92);
}

#calendar tbody td.today {
  background-color: #0e708d;
  width: 10%;
  height: 30px;
  color: #fff;
}


@media (min-width: 768px) {
  .calendar {
    padding-top: 15px;
  }

  .calendar__body {
    line-height: 30px;
    font-size: 20px;
  }

  .calendar__arrow {
    font-size: 35px;
    padding-bottom: 30px;
  }

  .calendar__month {
    font-size: 30px;
    padding-bottom: 30px;
  }

  .calendar__day {
    font-size: 25px;
  }

  #calendar tbody td.today {
    height: 40px;
    color: #fff;
  }
}

.record__btn {
  display: block;
  align-items: center;
  margin: 0 auto;
  width: 290px;
  height: 65px;
  text-decoration: none;
  color: $text-btn;
  font-family: "Open Sans", "Calibri", sans-serif;
  font-size: 24px;
  line-height: 50px;
  font-weight: 700;
  background-color: $main-col;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5px;
  margin-top: 35px;
  border-radius: 10px;
}
